import { useState, useEffect } from "react";
import { Spinner2 } from "../../../../../components/spinner";
import { fetchData } from "../../../../../helpers/utilities";
import { OrderDetail } from "./order-detail";
import { format, toDate } from "date-fns";
import { cedisLocale } from "./../../../../../helpers/utilities";

const OrdersComponent = ({ state }) => {
	const [orders, setOrders] = useState([]);
	const [selected, setSelected] = useState({});
	const [timeOut, setTimeOut] = useState(false);

	const [mode, setMode] = useState({
		edit: false,
		data: {},
	});

	const handleEdit = (data, editMode) => {
		setMode({
			edit: editMode,
			data,
		});
	};

	const selectItem = (item) => {
		setSelected(item);
		setMode({
			...mode,
			edit: false,
		});
	};

	const fetchOrders = (status) => {
		fetchData(`orders?status=${status}`).then((res) => {
			setOrders(res.data?.data);
		});
	};

	useEffect(() => {
		fetchOrders(state);
	}, [state]);

	return (
		<div className="components">
			<div className="row">
				<div className="col-5">
					{orders.length === 0 ? (
						<>
							{timeOut === false ? (
								<>
									<Spinner2 />
									{setTimeout(() => {
										setTimeOut(true);
									}, 1000)}
								</>
							) : (
								<p>No records found..</p>
							)}
						</>
					) : (
						<table className="table table-striped table-hover">
							<thead>
								<tr>
									<th>Customer</th>
									<th>Mode</th>
									<th>Order Total</th>
									{/* <th>Status</th> */}
								</tr>
							</thead>
							<tbody>
								{orders.map((order) => (
									<tr
										key={order.id}
										onClick={() => selectItem(order)}
										className={`${order.id === selected.id && "bg-info"}`}>
										<td>{order.customerName}</td>
										<td>{order.paymentMode}</td>
										<td>{cedisLocale.format(order.orderTotal)}</td>
										{/* <td>{order.status}</td> */}
										<td>{format(new Date(order.orderDate), "EEE, MMM d")}</td>
									</tr>
								))}
							</tbody>
						</table>
					)}
				</div>
				<div className="col-7">
					<div className="shadow detail-view">
						<OrderDetail
							order={selected}
							onEdit={handleEdit}
							onReload={() => {
								fetchOrders(state);
								setSelected({});
							}}
						/>
					</div>
				</div>
			</div>
		</div>
	);
};

export { OrdersComponent };
